<template>
  <div class="animate__animated animate__fadeIn">
    <el-container>
      <el-header>
        <el-input :maxlength="25" v-model="article.articleTitle" placeholder="..." show-word-limit style="width: 400px;margin-left: 10px"></el-input>

        <span style="margin-left: 10px;font-size: 17px"></span>
        <el-tag
          :key="tag"
          v-for="tag in dynamicTags"
          closable
          :disable-transitions="false"
          @close="handleClose(tag)" style="margin-left: 10px">
          {{tag}}
        </el-tag> 
        <el-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm">
        </el-input>
        <el-button v-else class="button-new-tag" type="primary" size="small" @click="showInput">+Tag</el-button>

        <!-- / -->
        <span style="margin-left: 10px;font-size: 17px"></span>
        <el-select v-model="article.articleType" placeholder="">
          <el-option value=""></el-option>
          <el-option value=""></el-option>
        </el-select>
        <div style="float: right;position: relative;">
          <el-button type="primary" @click="saveBlog()"></el-button>
        </div>
      </el-header>

      <el-main>
        <!-- markdown -->
        <div class="markdown_body">
          <mavon-editor 
          ref="md"
          v-model="content"
          @save="saveSubmit"
          @change="change" 
          @imgAdd="uploadImg"
          @imgDel="imgDel"
          :ishljs=true
          style="min-height: 700px" />
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { isNotNullORBlank } from '@/utils/utils'
import { base, postRequest, getRequest, deleteRequest, uploadFileRequest} from '@/utils/api'
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import 'mavon-editor/dist/markdown/github-markdown.min.css'
import '@/utils/plugins/hljs'
exportdefault{
 components:{
 mavonEditor, //mavon-editor
},
  data(){
   return{
      imgName: '',
content: '',//markdown
html: '',//html
      // 
      article: {
        // article_id: '',
        articleTitle: '',
        articleType: '',
      },

      dynamicTags: ['java'],
      inputVisible: false,
      inputValue: '',

      aid: '',  //   
  }
  },
  methods:{
    //html
change(value,render){    // value => markdown   render => html 
this.html=render;
      this.content = value;
// console.log(this.html);
      //console.log(this.content);
},
    //
    saveBlog() {
      if (this.article.articleTitle === "" || this.content === "") {
        this.$message.warning(' ');
        return;
      };
      var url = '';
      if(isNotNullORBlank(this.aid)){
        url = `/article/change/${this.aid}`
        console.log("")
      } else {
        url = '/article/add'
        console.log("")
      }
      let param = {
        articleTitle: this.article.articleTitle,
        articleContentMd: this.content,
        articleSummary: this.html,
        articleTag: this.dynamicTags.toString(),
        articleType: this.article.articleType
      }
      postRequest(url, param).then(resp => {
        console.log(resp)     
        if (resp.data.success) {
          this.$message.success(' => ^_^');
          this.$notify({
            title: '',
            dangerouslyUseHTMLString: true,
            duration: 0,
            message: '<strong><a href="/" target="_blank"></a></strong>'
          });
        } else {
          this.$message.warning('');
        }
      },
      error => {
        this.$message.warning('');
      });
    },
    // markdown
    saveSubmit(value, render) {
      this.saveBlog();
    },
//md
uploadImg (pos, $file) {
      var formdata = new FormData();
      formdata.append('file', $file);
      uploadFileRequest('/file/add', formdata).then(resp => {
        // console.log(resp)
        if (resp.data.success) {
          var url = resp.data.data;
          this.$refs.md.$img2Url(pos, url);
        } else {
          this.$message.error(resp.message)
        }
      }).catch(err => {
          console.log(err)
      })
    },
    // 
    imgDel(pos){
      var imgPlace = pos[0].slice(37); // 
      // console.log(imgPlace);
      deleteRequest('/file/delete/' + imgPlace).then(resp => {
        // console.log(resp)
      })
    }, 

    // 
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    showInput() {
      let tags = this.dynamicTags;
      if (tags.length > 3) {
        this.$message.warning('');
        return 
      }
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    },

    // 
    getUpdatePageInfo(aid) {
      getRequest(`/article/blog/${aid}`).then(resp => {
        // console.log(resp);
        this.content = resp.data.article_md;
        this.articles.article_title = resp.data.article_title;
        this.articles.article_author = resp.data.article_author;
        this.dynamicTags = resp.data.article_tag.split(',');
      });
    }
  },

  mounted() {
    if (isNotNullORBlank(this.$route.query.article_id)){
      this.aid = this.$route.query.article_id;
      this.getUpdatePageInfo(this.$route.query.article_id);
    }
  },
}
</script>

<style>
  .el-tag + .el-tag {
    position: relative;
    margin-left: 10px;
  }
  .button-new-tag {
    position: relative;
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    position: relative;
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  } 
</style>


